* {
  box-sizing: border-box;
  margin: 0;
  display: block;
}

.page {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  width: 100%;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  width: 100%;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__stars {
  width: 96px;
}

.card__reviews-label {
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  text-decoration: none;
}

.stars {
  justify-content: space-between;
  display: flex;
}

.stars__item {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 .stars__item:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__button--styles {
  color: #fff;
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
}

.card__button--styles:hover {
  color: #00acdc;
